import axios from 'axios';
import moment from 'moment';
import { v4 as uuidv4 } from 'uuid';

export default {
    namespaced: true,
    state: () => ({
       currentWeek: new Date().getWeek() + '.' + new Date().getMonth() + '.' + new Date().getWeekYear(),
       monday: Date.monday().toString(), // I store in string because computed property is not updating correctly either way
       sunday: Date.sunday().toString(),
       projectListWRData: [],
       supportListWRData: [],
       timeAwayListWRData: [],
       administrativeListWRData: [],
       showNotes: false,
    }),
    getters: {
        currentWeek: (state) => state.currentWeek,
        monday: (state) => state.monday,
        sunday: (state) => state.sunday,
        projectListWRData: (state) => state.projectListWRData,
        supportListData: (state) => state.supportListWRData,
        timeAwayListData: (state) => state.timeAwayListWRData,
        administrativeListData: (state) => state.administrativeListWRData,
        showNotes: (state) => state.showNotes,
    },
    mutations: {
        setProjectListWRData: (state, payload) => {
            state.projectListWRData = payload;
        },
        setSupportListWRData: (state, payload) => {
            state.supportListWRData = payload;
        },
        setAdministrativeWRData: (state, payload) => {
            state.administrativeListWRData = payload;
        },
        setTimeAwayListWRData: (state, payload) => {
            state.timeAwayListWRData = payload;
        },
    },
    actions: {
        getAllSubactivitiesData: async (context) => {
            let administrativeListWRURL = opentext.mainurl + getApis().administrativeList;
            let administrativeListData = await axios.get(administrativeListWRURL, { headers: { 
                'OTCSTicket': opentext.OTCSTicket, 
            }});
            administrativeListData = JSON.parse(administrativeListData.data.data).result;
            administrativeListData.pop();
            context.commit('setAdministrativeWRData', administrativeListData);

            let projectListWRURL = opentext.mainurl + getApis().projectList;
            let projectListData = await axios.get(projectListWRURL, { headers: { 
                'OTCSTicket': opentext.OTCSTicket, 
            }});
            projectListData = JSON.parse(projectListData.data.data).result;
            projectListData.pop();
            context.commit('setProjectListWRData', projectListData);

            let supportListWRURL = opentext.mainurl + getApis().supportList;
            let supportListData = await axios.get(supportListWRURL, { headers: { 
                'OTCSTicket': opentext.OTCSTicket, 
            }});
            supportListData = JSON.parse(supportListData.data.data).result;
            supportListData.pop();
            context.commit('setSupportListWRData', supportListData);

            let timeAwayListWRURL = opentext.mainurl + getApis().timeAwayList;
            let timeAwayListData = await axios.get(timeAwayListWRURL, { headers: { 
                'OTCSTicket': opentext.OTCSTicket, 
            }});
            timeAwayListData = JSON.parse(timeAwayListData.data.data).result;
            timeAwayListData.pop();
            context.commit('setTimeAwayListWRData', timeAwayListData);
        }
    }
}