<template>
    <div>
        <v-input>
            <span v-html="displayWeek"></span>
        </v-input>
    </div>
</template>

<script>
  import moment from 'moment';
  import { mapGetters } from 'vuex';
  export default {
    name: 'TheWeek',

    components: {
    },

    computed: {
      ...mapGetters('common', [
        'currentWeek',
        'monday',
        'sunday'
      ]),
      displayWeek: function(){
        const week = this.currentWeek.split('.')[0];
        const year = this.currentWeek.split('.')[2];
        const startOfWeek = moment(this.monday).format('DD-MMM');
        const endOfWeek   = moment(this.sunday).format('DD-MMM');
        return "Week <b>" + week + "</b> - " + year + " (" + startOfWeek + "/" + endOfWeek +")";
      }
    }
  }
</script>
