<template>
     <v-container fluid>           
        <v-row>
            <v-col cols="1">
                <div class="py-12">
                    <v-btn icon @click="previousRow"><v-icon>mdi-arrow-left-bold</v-icon></v-btn>
                </div>
            </v-col>
            <v-col cols="10">
                <table class="claim-table mt-7">
                    <thead>
                        <tr>
                            <th class="vugar-header">Activity</th>
                            <th class="vugar-header">SubActivity</th>
                            <th class="vugar-header">Monday({{mondayOfTheWeek}})</th>
                            <th class="vugar-header">Tuesday({{tuesdayOfTheWeek}})</th>
                            <th class="vugar-header">Wednesday({{wednesdayOfTheWeek}})</th>
                            <th class="vugar-header">Thursday({{thursdayOfTheWeek}})</th>
                            <th class="vugar-header">Friday({{fridayOfTheWeek}})</th>
                            <th class="vugar-header">Saturday({{saturdayOfTheWeek}})</th>
                            <th class="vugar-header">Sunday({{sundayOfTheWeek}})</th>
                            <th class="vugar-header" v-if="showNotes">Notes</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        
                    </tbody>
                    <tfoot>
                        <tr>
                            <td class="vugar-footer"></td>
                            <td class="vugar-footer"></td>
                            <td class="vugar-footer">SUM: {{getSum('monday')}}&nbsp;/&nbsp;OT: {{getOtSum('monday')}}</td>
                            <td class="vugar-footer">SUM: {{getSum('tuesday')}}&nbsp;/&nbsp;OT: {{getOtSum('tuesday')}}</td>
                            <td class="vugar-footer">SUM: {{getSum('wednesday')}}&nbsp;/&nbsp;OT: {{getOtSum('wednesday')}}</td>
                            <td class="vugar-footer">SUM: {{getSum('thursday')}}&nbsp;/&nbsp;OT: {{getOtSum('thursday')}}</td>
                            <td class="vugar-footer">SUM: {{getSum('friday')}}&nbsp;/&nbsp;OT: {{getOtSum('friday')}}</td>
                            <td class="vugar-footer">SUM: {{getSum('saturday')}}&nbsp;/&nbsp;OT: {{getOtSum('saturday')}}</td>
                            <td class="vugar-footer">SUM: {{getSum('sunday')}}&nbsp;/&nbsp;OT: {{getOtSum('sunday')}}</td>
                            <td class="vugar-footer" v-if="showNotes"></td>
                            <td></td>
                        </tr>
                    </tfoot>
                </table>
            </v-col>
            <v-col cols="1">
                <div class="py-12">
                    <v-btn icon @click="nextRow"><v-icon>mdi-arrow-right-bold</v-icon></v-btn>
                </div>
            </v-col>
        </v-row> 
    </v-container>
</template>

<script>
import moment from 'moment';
import { mapGetters } from 'vuex';
import { v4 as uuidv4 } from 'uuid';

export default {
    name: 'TheInsertWeek',
    data: () => {
        return {
            rows: [ { id: uuidv4() } ],
            weekMap: new Map(),
            comment: undefined,
        }
    },
    computed: {
        ...mapGetters('common', [
            'monday',
            'sunday',
            'showNotes'
        ]),
        mondayOfTheWeek: function(){
            return moment(new Date(this.monday)).format('DD'); 
        },
        tuesdayOfTheWeek: function(){
            return moment(new Date(this.monday).tuesday()).format('DD');
        },
        wednesdayOfTheWeek: function(){
            return moment(new Date(this.monday).wednesday()).format('DD');
        },
        thursdayOfTheWeek: function(){
            return moment(new Date(this.monday).thursday()).format('DD');
        },
        fridayOfTheWeek: function(){
            return moment(new Date(this.monday).friday()).format('DD');
        },
        saturdayOfTheWeek: function(){
            return moment(new Date(this.monday).saturday()).format('DD');
        },
        sundayOfTheWeek: function(){
            return moment(this.sunday).format('DD');
        }
    },
    methods: {
        previousRow: function(){

        },
        nextRow: function(){

        },
        getSum: function(day){
            let sum = 0;
            if(this.rows !== undefined)
                this.rows.forEach(v=>{
                    if(v[day])
                        v[day].forEach((el,i)=>{
                            if(!isNaN(el)){
                                sum += parseInt(el, 10);
                            }
                        })
                });
            return sum;
        },
        getOtSum: function(day){
            let sum = 0;
            if(this.rows !== undefined)
                this.rows.forEach(v=>{
                    if(v[day])
                        v[day].forEach((el,i)=>{
                            if(isNaN(el) && el.startsWith('OT')){
                                sum += parseInt(el.slice(2), 10);
                            }
                        })
                });
            return sum;
        }
    }
}
</script>

